import { h } from "@stencil/core";
export class RaulFileUpload {
    render() {
        return (h("div", { class: "r-file-upload" },
            h("div", { class: "r-file-upload__label" }, this.label),
            h("div", { class: "r-file-upload__content" },
                h("div", { class: "r-file-upload__icon" },
                    h("raul-icon", { icon: "data-upload-8" })),
                h("slot", null))));
    }
    static get is() { return "raul-file-upload"; }
    static get originalStyleUrls() { return {
        "$": ["raul-file-upload.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-file-upload.css"]
    }; }
    static get properties() { return {
        "label": {
            "type": "string",
            "mutable": false,
            "complexType": {
                "original": "string",
                "resolved": "string",
                "references": {}
            },
            "required": false,
            "optional": false,
            "docs": {
                "tags": [],
                "text": ""
            },
            "attribute": "label",
            "reflect": true
        }
    }; }
}
